<template>
  <div class="search-box search-box-bg-null">
    <CreateForm :inline="inline" labelWidth="100px" :rules="rules" @eventSubmit="onSubmit" refNanme="sbmit" bntText="提交" :formData="formData" :formListItem="formListItem()"/>
  </div>
</template>

<script>
import CreateForm from "@/components/form";
export default {
  components:{
    CreateForm
  },
  data(){
    return{
        inline:false,
        formData:{
          title:'',
          name:'',
          tel:'',//必须手机号
          phone:'',//必须座机号
          phoneTel:'',//手机或者座机号
          age:'',
          des:'',
          state:'',//select
          type:[],//复选框
          sex:"",//false
          open:false,//开关
          datetime:"2019-09-09 00:00:00",           //默认时间
          datetimerange:["2019-09-09","2019-09-15"],//默认选择时间
          cascader:[], //已经选择的value值联动选择数据格式
          img:"",     //验证图片1
          imglist:[], //图片数组1
          content:"<p>这是默认内容基本内容</p>",//默认编辑器内容
        },
        rules:this.verification()
    }
  },
  methods:{
    onSubmit({data}){
      //验证通过后执行
      console.log(data);

    },
    //表单验证
    verification(){
      //自定义验证
      var validatePass2 = (rule, value, callback) => {
        if (value!==""&&value.split(",").length<2) {
          callback(new Error('请上传图片至少上传2张!'));
        } else {
          callback();
        }
      };

      var v=[
         {field:"title",type:{required:"请填写标题！"},textCount:[6,25]},
         {field:"name",type:{required:"请输入作者名称！",chinese:'必须中文'},textCount:[2,9]},
         {field:"tel",type:{required:"请输入手机号！",tel:'必须是正确的11为手机号'},textCount:[11]},
         {field:"phone",type:{phone:'必须格式：xxx-xxxxxxx'},textCount:[11]},
         {field:"phoneTel",type:{required:"请输入座机号码或者手机号码！",phoneTel:'必须格式：xxx-xxxxxxx或者11位手机号'},textCount:[11]},
         {field:"img",validator:validatePass2,type:{required:"请上传图片！"}}, //自定义函数验证
         {field:"age",type:{number:'必须是数字'},textCount:[1,3]},
      ];
      v=this.$verification(v);
      return v;
    },
    formListItem(){
      let item=[
         {prop:"title", maxlength:25,placeholder:"请输入文章标题",label:'标题：',type:'text'},//普通text
         {prop:"name", maxlength:3,placeholder:"请输入姓名",label:'姓名：',type:'text'},
         {prop:"tel", maxlength:11,placeholder:"请输手机号",label:'电话：',type:'text'},
         {prop:"phone", maxlength:12,placeholder:"请输座机号",label:'客服电话：',type:'text'},
         {prop:"phoneTel", maxlength:12,placeholder:"请输座机号或者手机号",label:'座机手机：',type:'text'},
         {prop:"age", maxlength:3,placeholder:"请输入年龄",label:'年龄：',type:'text'},
         {prop:"des", maxlength:30,placeholder:"请输入描述",label:'描述：',type:'textarea',height:"auto"}, //只有textarea设置高度
         {prop:"img", label:'图片1：',type:'imgUpload',placeholder:'图片做多上传3张，建议尺寸：288*188;',
            uploadParame:{
               data:{path:"active"},//上传图片时携带图片参数
               limit:3,//图片上传最大数量
               action:this.$http+"index.php/main/upload_file", //上传图片地址
            },
            name:'imglist' //formData对应key值名，formData[key]必须是数组
         },//如果是上传图片
         {prop:"state",placeholder:"请选择状态",label:'状态：',type:'select',select:[
           {text:"已发布",value:"ok"},
           {text:"未发布",value:"on"}
         ]},//select
         {prop:"type",label:'爱好：',minlength:0,maxlength:2,type:'text',type:'checkbox',checkbox:[
           {text:"兵乓",value:"1"},
           {text:"唱歌",value:"2"},
           {text:"听歌",value:"3"},
           {text:"羽毛球",value:"4"}
         ]},//复选框
         {prop:"sex",label:'唯一爱好：',type:'radio',radio:[
           {text:"兵乓",value:"1"},
           {text:"唱歌",value:"2"},
           {text:"听歌",value:"3"},
           {text:"羽毛球",value:"4"}
         ]},//单选
         {prop:"open",label:'开关：',type:'switch'},//开关
         //日期时间选择器type：年-月-日 时：分：秒 datetime；，年-月-日：date
         {prop:"datetime",label:'开发日期：',type:'datetime',placeholder:'选择日期时间'},
         //日期时间区间选择器 type：年-月-日 时：分：秒 datetimerange，年-月-日：daterange
         {prop:"datetimerange",label:'时间范围：',type:'daterange',range:'至',startPlaceholder:'开始日期',endPlaceholder:'结束日期'},
         //联动数据options
         {prop:"cascader",label:'联动选择：',type:'cascader',placeholder:'选择联动数据',options:[
           {
             key:"1",
             value:"qdkf",
             label:"前端开发",
             children:[
                {value: 'css',label: '样式'},
                {value: 'js',label: 'javascript',
                  children:[
                    {value:'vue',label:"vue"},
                    {value:'react',label:"react"}
                  ]
                },
                {value: 'html',label: 'HTML'}
             ]
           }
         ]},
         {prop:"content",label:'内容：',type:'editConten',height:"auto"}
       ];
       return item;
    }
  }
}
</script>

<style scoped>
.search-box{
  background: #f2f2f2;
  padding:10px 10px 4px 10px;
  margin: 10px 0;
}
.search-box .el-form-item{
  margin-bottom: 6px;
}
.search-box-bg-null{
  background: #fff;
}
</style>



