import Vue from 'vue'
import Router from 'vue-router'
//首页
import Home from '@/page/index';
//登录
import Login from '@/page/login';

// 系统管理
import Admin from '@/page/admin/operation/admin';//管理员
import Role from '@/page/admin/operation/role';//角色
import SetAdmin from '@/page/admin/operation/setAdmin';//设置
import Auth from '@/page/admin/operation/auth';//权限栏目
//文章管理
import Article from '@/page/admin/article';//文章管理


//测试封装使用演示
import DemoForm from '@/page/component/demo/dform';//form
import DemoUpload from '@/page/component/demo/dupload';//图片上传
import DemoTable from '@/page/component/demo/dtable';//数据表格
import Dpage from '@/page/component/demo/dpage';//分页数据整合
import DdimExport from '@/page/component/demo/dimExport';//数据表格


Vue.use(Router)


export default new Router({
  // mode: "history",
  // base:"/dist",
  routes: [
    {path: '/',name: 'home',component: Home,meta: {title: "首页",type: "login", keepAlive:false}}, //keepAlive：开启缓存俩判断
    // 系统管理
    {path: '/admin',name: 'admin',component: Admin,meta: {title: "系统管理-管理员",type: "login", keepAlive:false}},
    {path: '/role',name: 'role',component: Role,meta: {title: "系统管理-角色管理",type: "login", keepAlive:false}},
    {path: '/setAdmin',name: 'setAdmin',component: SetAdmin,meta: {title: "系统管理-系统设置",type: "login", keepAlive:false}},
    {path: '/auth',name: 'auth',component: Auth,meta: {title: "系统管理-栏目管理",type: "login", keepAlive:false}},
    //登录
    {path: '/login',name: 'login',component: Login,meta: {title: "系统管理-登录页面",type:"", keepAlive:false}},
    //文章管理页面
    {path: '/article',name: 'article',component: Article,meta: {title: "内容管理-文章管理",type:"login", keepAlive:false}},
    // 后台demo
    {path: '/demoForm',name: 'demoForm',component: DemoForm,meta: {title: "演示-form演示",type:"login",keepAlive:false}},
    {path: '/demoUpload',name: 'demoUpload',component: DemoUpload,meta: {title: "演示-图片上传",type:"login",keepAlive:false}},
    {path: '/demoTable',name: 'demoTable',component: DemoTable,meta: {title: "演示-数据表格",type:"login",keepAlive:false}},
    {path: '/dpage',name: 'dpage',component: Dpage,meta: {title: "演示-分页数据",type:"login",keepAlive:false}},
    {path: '/dimExport',name: 'dimExport',component: DdimExport,meta: {title: "演示-导入导出",type:"login",keepAlive:false}},
  ]
})



