import store from '../store';

let {userInfo,operation}=store.getters.getData;

let Verification=(name)=>{
  //验证操作按钮
  let off=operation.some((item)=>{
     return item.name===name;
  })
  return off;
}


export default{
    base_url:"http://vue.admin.com:8888/",
    site_url:"http://vue.admin.com:8888/",
    userSID:userInfo.id,
    user_name:userInfo.user_name,
    user_pass:"qdvld",
    handleVerify:Verification
}



