<template>
  <div>
    <div class="item">
      <h2 class="title">1.单上传图片</h2>
      <Upload item="imageUrl" name="img1" :imgList="imageUrl" :parame="uploadParame1" @onSuccess="getImage"></Upload>
    </div>

    <div class="item">
      <h2 class="title">2.单张多上传图片</h2>
      <Upload  item="imgeUrl2" name="img2" :imgList="imgeUrl2" :parame="uploadParame2" @onSuccess="getImage"></Upload>
    </div>

    <div class="item">
      <h2 class="title">3.图片裁剪上传</h2>
      <Upload :croppParame="croppParame" :isCrop="true" item="imgeUrl3" name="img3" :imgList="imgeUrl3" :parame="uploadParame2" @onSuccess="getImage"></Upload>
    </div>



  </div>
</template>

<script>
 import Upload from '@/components/upload';
 export default {
   components:{Upload},
   data(){
     return{
       imageUrl:[],
       imgeUrl2:[],
       imgeUrl3:[],
       uploadParame1:{
         data:{path:"admin"},//上传图片时携带图片参数
         limit:1,//图片上传最大数量
         action:"index.php/main/upload_file",//上传图片地址
       },
       uploadParame2:{
         data:{path:"list"},//上传图片时携带图片参数
         limit:3,//图片上传最大数量
         action:"index.php/main/upload_file",//上传图片地址
       },
       croppParame:{   //裁剪配置
           croppersW:"800px",//裁剪显示区域大小（像素）
           croppersH:"400px",//裁剪显示区域大小（像素）
           autoCropWidth:"600", //裁剪框的宽度（无）
           autoCropHeight:"200", //裁剪框的高度（无）
           top:"30vh",           //裁剪后弹窗顶部距离
           topCrop:"20vh"       //裁剪前弹窗顶部距离
       }
     }
   },
  methods:{
    getImage({set,imgUrl,index}){
      if(set.type==="add"){
        //添加
        this[index].push(imgUrl);
      }else if(set.type==="edit"){
        //编辑
        this.$set(this[index],set.index,imgUrl);
      }else if(set.type==="del"){
        //删除
       this[index]=this[index].filter((item,i)=>{
          if(set.index!=i){
            return item;
          }
        });
      }

    }

  }
 }
</script>

<style scoped>
.title{
   padding: 20px 0;
}
</style>



