<template>
  <div class="form-box">
    <CreateForm ref="myForm" labelWidth="90px" :rules="rules" @eventSubmit="onSubmit" refNanme="sbmit" bntText="提交" :formData="formData" :formListItem="formListItem()"/>
  </div>
</template>

<script>
  import CreateForm from "@/components/form";
  export default {
    components:{CreateForm},
    name:"addForm",
    data(){
      return{
          formData:this.setFormData(),
          rules:this.verification()
      }
    },
    methods:{
        showFormData({data,type}){
           //设置默认数据
           if(type==="edit"){
             let {id,title,imgurl,keyword,describe,content,state}=data.row;
             let d={
               title:title,
               keyword:keyword,
               describe:describe,
               content:content,
               state:state==='on' ? false : true,
               img:imgurl,
               imageUrl:[]
             }
             if(imgurl){
                d.imageUrl=imgurl.split(",");
             }
             this.formData=d;
             this.$refs.myForm.getEditor(d.content,0);//如果用的编辑器就调用
             //this.$refs.myForm.getEditor("sadfsadf111111sadf",1);//多个编辑器时
           }else{
             this.formData=this.setFormData();
             this.$refs.myForm.clearEditor(0);//清空编辑器内容
             //this.$refs.myForm.clearEditor(1);//多个时
           }
        },
        onSubmit({data}){
          //数据验证通过
          this.$emit('evnetPost',data);
        },
        setFormData(){
          //配置数据
          return {
            title:"",
            keyword:"",
            describe:"",
            content:"",
            state:false,
            img:"",
            imageUrl:[]
          }
        },
        verification(){
          //自定义验证
          var vimg = (rule, value, callback) => {
           // console.log(value)
            if (value!==""&&value.split(",").length<1) {
              callback(new Error('请上传图片至少上传1张!'));
            } else {
              callback();
            }
          };
           //内容验证
          var vcontent=(rule, value, callback)=>{
            //console.log(value.replace(/<[^>]*>/g))
            if (value!==""&&value.replace(/<[^>]*>/g,'')==="") {
              callback(new Error('请输入文章内容!'));
            } else {
              callback();
            }
          };
          //验证数据
          var v=[
             {field:"title",type:{required:"请填写标题！"},textCount:[2,25]},
             {field:"img",validator:vimg,type:{required:"请上传图片！"}}, //自定义函数验证
             {field:"content",validator:vcontent,type:{required:"请输入文章内容！"}}
          ];
          v=this.$verification(v);
          return v;
        },
        formListItem(){
          //配置表单
          let form=[
            {prop:"title", maxlength:25,placeholder:"请输入文章标题",label:'标题：',type:'text'},
            {prop:"keyword", maxlength:255,placeholder:"请输入文章关键词(多个用英文逗号隔开)",label:'关键词：',type:'text'},
            {prop:"describe", maxlength:255,placeholder:"请输入文章摘要",label:'摘要：',type:'textarea',height:"auto"},
            {prop:"img", label:'图片1：',type:'imgUpload',placeholder:'图片建议尺寸：288*188;',
               uploadParame:{
                  data:{path:"active"},//上传图片时携带图片参数
                  limit:1,//图片上传最大数量
                  action:"index.php/main/upload_file", //上传图片地址
               },
               name:'imageUrl' //formData对应key值名，formData[key]必须是数组
            },
            {prop:"state",label:'是否发布：',type:'switch'},//发布状态
            {prop:"content",label:'内容：',type:'editConten'}
          ];
          return form;
        }
    }
  }
</script>

<style scoped>
  .item{
    background: green;
    margin-bottom: 20px;
    height: 180px;
  }
  .form-box{
    padding-top: 15px;
    padding-right: 15px;
    padding-bottom: 15px;
  }
</style>



