<template>
  <el-drawer
    size='80%'
    :wrapperClosable='true'
    :show-close="false"
    :visible.sync="visible"
    direction="rtl"
    custom-class="drawer-box"
    @close="handleClose">
    <div slot="title" class="title">{{title}}<div class="icon" @click="close('hide')"><i class="el-icon-close"></i></div></div>
    <div class="drawerContent">
      <AddForm ref="addform" @evnetPost="submit" />
    </div>
  </el-drawer>
</template>

<script>
  import AddForm from '@/page/component/demo/add/addForm';//独立添加组件
  export default {
    components:{AddForm},
    name:'add',
    data(){
      return{
         visible:false,
         addData:{},
         title:""
      }
    },
    methods:{
      submit(d){
        let {type,data}=this.addData;
        //过去需要的数据
        let postdata={
          title:d.title,
          keyword:d.keyword,
          describe:d.describe,
          imgurl:d.img,
          content:d.content,
          state:d.state? 'ok' : 'on',
          type:type
        };
        if(type==='edit'){
          postdata.id=data.row.id;
        }
        this.postHttp(postdata);
      },
      //操作弹窗
      handleClose(){
        //console.log('关闭后回调')
      },
      close(t){
        //hide:手动关闭
        //update:添加成功数据更新
        this.$emit('resetData',{
          hideType:t
        })
        this.visible=false;
      },
      show(field){
        //初始化基本操作类型在这里处理
        this.addData=field;
        let {type,data}=field;

        if(type==='add'||type==='edit'){
          this.title=type==='add' ? "添加文章" : "编辑文章";
          this.visible=true;
          setTimeout(()=>{
             this.$refs.addform.showFormData(field);
          },100)
        }else if(type==='del'){
           //console.log("shanchu")
            this.postHttp({
              id:data.row.id,
              type:type
            });
           
        }else if(type==='issuer'||type==="issuers"){
           this.postHttp({
              id:data.row.id,
              type:type
           });
        }
      },
      postHttp(d){
        const loading = this.$loading({
          lock: true,
          text: '正在处理...',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'
        });
        this.$request({
        	type:"post",
        	url:"index.php/apiAll/addArticle",
          data:d,
        	success:(res)=>{
            loading.close();
            if(res.code==200){
              this.$message({
                showClose: true,
                message: res.msg,
                type: 'success'
              });
              this.close('update')
            }else{
             this.$message({
               showClose: true,
               message: res.msg,
               type: 'error'
             });
            }
        	},error:(err)=>{
        		this.$message.error(err);
        	}
        });
      }
    }
  }
</script>

<style scoped>

.title{
  padding:0 20px;
  height:60px;
  line-height: 60px;
  font-size: 16px;
  border-bottom: 1px solid #f7f7f7;
}
.title .icon{
 float: right;
 cursor: pointer;
}
.drawerContent{
 height: 93vh;
 background: #FFFFFF;
 overflow: scroll;
}
/* .bottom{
  height: 12vh;
  background:#FFFFFF;
} */
</style>



