<template>
  <div>
    <div class="search-box search-box-bg-null">
      <el-button type="primary" size="small" round  @click="operation('add')">添加文章<i class="el-icon-plus el-icon--right"></i></el-button>
    </div>
    <!-- 数据表格 -->
    <div class="main">
      <!-- 数据表格 -->
      <TbaleList :tableData="tableData" :unfold="true" @tabBtnOperation="eventOperation" :column="column"/>
      <!-- 分页组件 -->
      <PageBott @currChange="pageChang" :pageMeter="pageMeter" />
    </div>
    <!-- 添加组件 -->
    <Add @resetData="resetData" ref="add"/>
  </div>

</template>

<script>
  import PageBott from '@/components/pageBott';//分页组件
  import TbaleList from '@/components/table';//表格展示数据
  import Add from '@/page/component/demo/add/add';//独立添加组件


  export default {
    components:{
      PageBott,TbaleList,Add
    },
    data() {
      return {
        title:"",
        //分页配置
        pageMeter:{
          limit:10,
          page:1,
          total:0
        },
        //数据表格配置
        tableData:[],//表格数据
        column:[
          //配置列表显示数据
          {prop:"id",name:"ID",minWidth:100,isShowType:"default"},
          {prop:"imgurls",name:"缩略图",minWidth:170,isShowType:"img"},
          {prop:"title",name:"文章标题",minWidth:170,isShowType:"default",isUnfold:true},
          {prop:"creater",name:"原创作者",minWidth:170,isShowType:"default"},
          {prop:"create_time",name:"创建时间",minWidth:170,isShowType:"default"},
          {prop:'keyword',name:"关键词",isShow:true,isUnfold:true},
          {prop:'describe',name:"文章摘要",isShow:true,isUnfold:true},
          {prop:'issuer',name:"发布人",isShow:true,isUnfold:true},
          {prop:'issuer_time',name:"发布时间",isShow:true,isUnfold:true},
          {prop:'update_time',name:"更新时间",isShow:true,isUnfold:true},
          {prop:'state_text',name:"文章状态",isUnfold:true},
          {prop:'state',name:"操作",minWidth:200,align:'center',fixed:"right",isShowType:"btn",btnArr:[
             {size:"mini",type:"primary",plain:true,name:"编辑",field:'edit'},
             {size:"mini",type:"success",plain:true,name:"发布",field:'issuer',state:'ok'},
             {size:"mini",type:"warning",plain:true,name:"取消发布",field:'issuers',state:'on'},
             {size:"mini",type:"danger",plain:true,name:"删除",field:'del'}
          ]}
        ]
      }
    },
    created(){
      //页面加载默认获取数据
      this.getData(this.searchData());
    },
    methods: {
      //表单组件处理
      resetData({hideType}){
        if(hideType==='update'){
          //添加后更新数据数据更新
           this.getData(this.searchData());
        }
      },
      eventOperation({type,data}){
        //表格操作按钮时间返回
        this.operation(type,data);
      },
      operation(type,data){
        console.log(type,data);
        this.$refs.add.show({
          type:type,
          data:data
        });
      },
      getData(d){
        //表格数据获取
        const loading = this.$loading({
          lock: true,
          text: '加载中',
          spinner: 'el-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'
        });
        this.$request({
        	type:"get",
        	url:"index.php/apiAll/article",
          data:d,
        	success:(res)=>{
            loading.close();
            if(res.code==200){
               this.tableData=res.data.data;
               this.pageMeter.total=res.data.count;
            }else{
              this.$message({
                showClose: true,
                message: '数据加载失败',
                type: 'error'
              });
            }

        	},error:(err)=>{
        		this.$message.error(err);
        	}
        });
      },
      pageChang({limit,page}){
        //分页处理数据
        this.pageMeter.limit=limit;
        this.pageMeter.page=page;
        this.getData(this.searchData());
      },
      searchData(){
        //配置获取数据参数
        return {
          limit:this.pageMeter.limit,
          page:this.pageMeter.page
        }
      }

    }
  }
</script>

<style scoped>
@import url("../../../assets/css/main.css");
</style>



