<template>
  <div>
    <div class="main">
       <div>
         <el-button @click="tab('123')" size="medium" round>中等按钮</el-button>
         <el-button @click="tab('啥都发啥')" size="small" round>小型按钮</el-button>
         <el-button @click="tab('1')" size="mini" round>超小按钮</el-button>
         <el-button @click="tab('')" size="mini" round>全部</el-button>
         {{extra.title}}
       </div>
        <tablePage ref="tablePage" :extra="extra"></tablePage>
    </div>
  </div>

</template>

<script>

  import tablePage from '@/page/component/demo/datapage/tablePage';//独立添加组件
  export default {
    components:{tablePage},
    data() {
      return {
        extra:{
          title:""
        }
      }
    },
    created(){
      //页面加载默认获取数据
     // this.getData(this.searchData());
    },
    methods: {
      tab(t){
        this.extra.title=t;
        this.$refs.tablePage.loadTable(true)
      }
    }
  }
</script>

<style scoped>
@import url("../../../assets/css/main.css");
</style>



