<template>
 <div>
      <div class="btn-box btn-box-p-top">操作日志操作</div>
      <!-- 数据表格 -->
      <div class="data-list-box">
        <el-table
        ref="filterTable"
          :data="tableData"
           row-key="id"
          :header-cell-style="headerClassName"
          max-height="700"
          style="width: 100%">
          <el-table-column prop="id" label="ID" width="150"></el-table-column>
          <el-table-column prop="name" label="管理员" width="180"></el-table-column>
          <el-table-column prop="type" label="操作类型" width="180"></el-table-column>
          <el-table-column prop="time" width="180" label="操作时间"></el-table-column>
          <el-table-column prop="details" label="操作内容"></el-table-column>
        </el-table>
      </div>
      <!-- end数据表格 -->
 </div>
</template>

<script>
  export default {
    data(){
      return{
        tableData: []//表格数据
      }
    },
    created(){
      this.getData();
    },
    methods:{
      headerClassName({row, rowIndex}){
        //设置表头样式
        return {"backgroundColor":"#eef1f6","color":"#000000"};
      },
      //获取表格数据
      getData(f){
          const loading = this.$loading({
            lock: true,
            text: '加载中',
            spinner: 'el-icon-loading',
            background: 'rgba(0, 0, 0, 0.7)'
          });
          this.$request({
          	type:"get",
          	url:this.$api.getLog,
          	success:(res)=>{
              loading.close();
              if(res.code==200){
                 this.tableData=res.data;
              }else{
                this.$message({
                  showClose: true,
                  message: '数据加载失败',
                  type: 'error'
                });
              }
          	},error:(err)=>{
          		this.$message.error(err);
          	}
          });
      }
    }
  }

</script>

<style scoped>
@import url("../assets/css/main.css");
</style>



