// www.qdvdl.com

import axios from 'axios';
import qs from 'qs';


//const apiUrl="http://gnjf.sctshd.com";//API地址
//const apiUrl="http://vue.admin.com/index.php";//API地址

//基础库
import config from './config';
const apiUrl=config.site_url; //API地址
const userSID=config.userSID;//当前登录的

// console.log(config);
let auth={
  username:config.user_name,
  password:config.user_pass,
};

const setHeader={
	'Content-Type': 'application/x-www-form-urlencoded'
}

//设置配置
const instance=axios.create({
  auth: auth,
	baseURL:apiUrl,
	withCredentials: true, //跨域
	responseType:"json",
	headers:setHeader,
	transformRequest: [function (data) {
    //console.log(data)
		if(data){
			data=qs.stringify(data)
		}
		return data;
	}]
})


function get(url,params,success,error){
	instance.get(url,{
		params:params
	}).then((res)=>{
		if(res.status===200){
			success(res.data)
		}else{
			error('请求发生错误,请检查接口异常!')
		}
	}).catch((err)=>{
		error('当前环境网络较差!');
	})
}

function post(url,params,success,error){
	instance.post(url,params).then((res)=>{
		if(res.status===200){
			success(res.data)
		}else{
			error('请求发生错误,请检查接口异常!');
		}
	}).catch((err)=>{
	//	console.log(err)
		error('当前环境网络较差!');
	})
}
let axiosPromiseArray=[];
function upload(url,params,success,error){
    let fConfig = {
      //添加请求头
      baseURL:apiUrl,
      headers: { "Content-Type": "multipart/form-data" },
      //添加上传进度监听事件
      onUploadProgress: e => {
        var completeProgress = ((e.loaded / e.total * 100) | 0) + "%";
        console.log(completeProgress);
      }
    };

    axiosPromiseArray.push(axios.post(url,params,fConfig));

    axios.all(axiosPromiseArray).then((res)=>{
        success(res)
    }).catch((err)=>{
      error('当前环境网络较差!');
      
    })

     //    }
     //    // 所有分片上传后，请求合并分片文件
     //    await axios.all(axiosPromiseArray).then(() => {


    // axiosPromiseArray.push
    // axios.post(url, params, fConfig).then((res)=>{
    //     success(res)
    // }).catch((err)=>{
    //     error('当前环境网络较差!');
    // });
}

//对外接口
export function request({type, url, data,success,error}){
    // let userSID=sessionStorage.getItem('userSID');
    if(type!="file"&&userSID){
      data=Object.assign({},data,{userSID:userSID });
    }
    if(type === 'get'){
        return get(url,data,success,error);
    }else if(type === 'post'){
        return post(url,data,success,error);
    }else if(type === 'file'){
        return upload(url,data,success,error);
    }
}



